@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  width: 100vw;
  max-width: 100%;
  box-sizing: border-box;
  background-color: #f8afa6;
  color: #f9f1f0;
  font-family: "Ubuntu", sans-serif;
}

.App {
  width: 100%;
  margin-top: 70px;
  margin-bottom: 30px;
  display: block;
  align-items: center;
  text-align: center;
}

/* navigation bar */
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #f79489;
  width: 100%;
  height: 70px;
  z-index: 3;
  padding: 0px 20px;
}

.message {
  color: red;
  margin-top: 20px;
}

.home-icon {
  width: 40px;
}

.nav-right {
  margin-right: 40px;
}

.nav-btn {
  margin: 0px 20px;
  width: 70px;
  height: 35px;
  color: #f9f1f0;
  background-color: transparent;
  border: 0;
  font-size: 18px;
  font-weight: 700;
  font-family: "Ubuntu", sans-serif;
}

.nav-btn:hover {
  cursor: pointer;
}

/* home page  */
.home-container {
  width: 100%;
  display: block;
}

.first-row {
  width: 100%;
  height: 50vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(/static/media/background001.2936775f.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  margin-bottom: 60px;
}

.first-row-text {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -50px;
  margin-left: -204px;
  text-align: center;
  font-family: "Kaushan Script", cursive;
}

.first-row-text > h1 {
  color: #fae1dd;
  font-size: 60px;
  font-weight: 700;
  -webkit-text-stroke: 1px #fae1dd;
  margin-bottom: 20px;
}

.first-row-text > h2 {
  color: #fae1dd;
  font-weight: 500;
  font-size: 30px;
}

.second-row {
  display: flex;
  height: 600px;
  width: 100%;
  justify-content: space-around;
}

.second-left {
  width: 25vw;
  background-image: url(/static/media/leftback.ee429546.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.second-middle {
  width: 25vw;
  background-image: url(/static/media/middleback.5e8b8ede.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
}

.second-right {
  width: 25vw;
  background-image: url(/static/media/rightback.67a47b54.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
}

.main-btn {
  margin: 30px;
  width: 200px;
  height: 40px;
  color: #f9f1f0;
  background-color: #f8afa6;
  border: 0;
  border-radius: 50px;
  font-size: 20px;
  font-weight: 700;
  font-family: "Ubuntu", sans-serif;
}

.main-btn:hover {
  cursor: pointer;
}

/* sign up page */
.signup-container {
  height: calc(100vh - 180px);
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.signup-container > h3 {
  font-size: 40px;
  margin: 40px;
}

.signup-container > form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.signup-container > form > label {
  transition: 0.2s;
  color: gray;
  margin: 5px;
  font-size: 20px;
}

.signup-container > form > input {
  color: gray;
  font-family: inherit;
  width: 100%;
  height: 10px;
  border: 0;
  border-bottom: 2px solid gray;
  font-size: 1.3rem;
  padding: 7px 0;
  background: transparent;
  margin-bottom: 20px;
}

.signup-container > form > button {
  color: #f9f1f0;
  width: 200px;
  height: 40px;
  font-size: 20px;
  background-color: pink;
  border: 0;
  border-radius: 50px;
}
.signup-container > form > button:hover {
  cursor: pointer;
  background-color: violet;
}

/* log in page */
.login-container {
  height: calc(100vh - 180px);
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.login-container > h3 {
  font-size: 40px;
  margin: 40px;
}

.login-container > form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-container > form > label {
  transition: 0.2s;
  color: gray;
  margin: 5px;
  font-size: 20px;
}

.login-container > form > input {
  color: gray;
  font-family: inherit;
  width: 100%;
  height: 10px;
  border: 0;
  border-bottom: 2px solid gray;
  font-size: 1.3rem;
  padding: 7px 0;
  background: transparent;
  margin-bottom: 20px;
}

.login-container > form > button {
  color: #f9f1f0;
  width: 200px;
  height: 40px;
  font-size: 20px;
  background-color: pink;
  border: 0;
  border-radius: 50px;
}
.login-container > form > button:hover {
  cursor: pointer;
  background-color: violet;
}

/* recipe list page */
.recipes-container {
  min-height: calc(100vh - 180px);
  background: linear-gradient(
      rgba(219, 209, 209, 0.863),
      rgba(204, 194, 194, 0.144)
    ),
    url(/static/media/background002.5fb6f981.jpeg);
  background-size: cover;
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search-box {
  margin-bottom: 30px;
}

.search-box > input {
  width: 400px;
  height: 30px;
  border: 0;
  border-radius: 50px;
  padding-left: 20px;
}

.list-title {
  font-size: 50px;
  font-weight: 700;
  -webkit-text-stroke: 2px #f8afa6;
}

.pages {
  margin: 20px 0px 0px 0px;
}

.pages > button {
  font-weight: 700;
  color: #f8afa6;
  font-size: 20px;
  border: 0;
  background-color: transparent;
}

.pages > button:hover {
  cursor: pointer;
}
.cards-box {
  display: flex;
  flex-wrap: wrap;
}

/* recipe card */
.card-box {
  display: flex;
  justify-content: center;
  width: 300px;
  height: 120px;
  background-color: #f8afa6;
  margin: 20px;
  border-radius: 10px;
}

.card-box:hover {
  background-color: #f79489;
}

.card-left {
  margin: 10px;
}

.card-left > img {
  width: 100px;
  height: 100px;
}

.card-title {
  font-size: 20px;
  color: inherit;
  text-decoration: none;
}

.card-right {
  width: 160px;
  position: relative;
  padding-top: 10px;
  margin: 0px 10px;
}
.card-text {
  color: gray;
  margin: 20px 0px;
  font-size: 12px;
  width: 160px;
  word-wrap: break-word;
}

.card-text > p {
  color: gray;
  margin-top: 5px;
  font-size: 12px;
}
.fav-btn {
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.fav-btn > button {
  border: 0;
  background-color: transparent;
  font-size: 20px;
}

.fav-btn > button:hover {
  cursor: pointer;
}
/* favorite */
.fa-heart {
  color: red;
}

/* detail page */
.container {
  min-height: calc(100vh - 180px);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.container > h1 {
  margin: 20px;
  font-size: 40px;
}
.detail-container {
  display: flex;
  margin: 0 5%;
  background-color: #fec89a;
}

.detail-left {
  padding: 34px;
  width: 50%;
}

.detail-left > img {
  width: 250px;
}

.detail-left > h4 {
  font-size: 20px;
  margin: 15px;
}

.detail-right > h4 {
  font-size: 20px;
  margin: 15px;
}

.ingredients {
  display: flex;
  flex-wrap: wrap;
}
.ingredients > p {
  margin: 3px;
}

.instructions {
  display: flex;
  flex-wrap: wrap;
  margin: 0px 20px 30px 0px;
}

.instructions > li {
  text-align: start;
  margin: 2px;
}

.edit-btn {
  font-size: 20px;
  font-weight: 700;
  color: #f9f1f0;
  background-color: #fec89a;
  width: 200px;
  height: 30px;
  border: 0;
  border-radius: 20px;
  margin: 30px 0px;
}

.edit-btn:hover {
  background-color: pink;
  cursor: pointer;
}

/* add page */

.title {
  margin: 20px;
  font-size: 40px;
}

.add-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.add-container > form {
  padding: 40px;
  background-color: #a49393;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.add-row {
  display: flex;
  margin-bottom: 5px;
}

.row-left {
  display: flex;
  justify-content: flex-end;
  width: 150px;
  font-size: 20px;
  font-weight: 700;
  margin: 5px;
}

.row-right {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 20px;
}

.row-right > input {
  padding-left: 20px;
  width: 200px;
  height: 20px;
  border: 0;
  border-radius: 20px;
}

.box {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.box > input {
  padding-left: 20px;
  width: 200px;
  height: 20px;
  border: 0;
  border-radius: 20px;
  margin-right: 10px;
}

.add-text {
  margin-top: 10px;
}

.add-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.mr10 {
  background-color: #fec89a;
  width: 60px;
  height: 20px;
  border: 0;
  border-radius: 20px;
  margin: 10px 0px;
}

.mr10:hover {
  background-color: pink;
  cursor: pointer;
}

.add-btn {
  font-size: 20px;
  font-weight: 700;
  color: #f9f1f0;
  background-color: #fec89a;
  width: 200px;
  height: 30px;
  border: 0;
  border-radius: 20px;
  margin: 10px 0px;
}
.add-btn:hover {
  background-color: pink;
  cursor: pointer;
}

.image-preview {
  width: 100px;
  height: 100px;
}
/* edit page */

.title {
  margin: 20px;
  font-size: 40px;
}

.edit-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.edit-container > form {
  padding: 30px;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #a49393;
}

.edit-container > form > label {
  font-size: 20px;
  margin: 10px;
  font-weight: 700;
}
.edit-container > form > input {
  padding-left: 20px;
  width: 200px;
  height: 20px;
  border: 0;
  border-radius: 20px;
}

.update-btn {
  font-size: 20px;
  font-weight: 700;
  color: #f9f1f0;
  background-color: #fec89a;
  width: 200px;
  height: 30px;
  border: 0;
  border-radius: 20px;
  margin: 20px 0px;
}
.update-btn:hover {
  background-color: pink;
  cursor: pointer;
}

/* profile page */
.container > h3 {
  font-size: 40px;
  margin: 20px;
}

.container > h4 {
  font-size: 20px;
  margin: 10px;
}

.container > h5 {
  font-size: 25px;
  margin: 10px 0px 30px 0px;
}

.profile-container {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.profile-left {
  width: 40%;
  min-width: 340px;
  background-color: #faedcd;
  border-radius: 10px;
}

.profile-left > h3 {
  font-size: 40px;
  padding: 5px;
  background-color: #d4a373;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.my-recipes {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.profile-right {
  width: 40%;
  min-width: 340px;
  background-color: #faedcd;
  border-radius: 10px;
}

.profile-right > h3 {
  font-size: 40px;
  padding: 5px;
  background-color: #d4a373;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.favorite {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.favorite > h1 {
  font-size: 30px;
  margin: 30px;
  color: #f79489;
}

/* footer */
#footer {
  border-top: 10px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(85, 82, 82);
  color: #fff;
  width: 100%;
  height: 70px;
}

.names {
  margin: 5px 0px;
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
}

.names > .fab {
  font-size: 15px;
  margin: 0px 5px;
}

.line {
  margin: 5px 0px;
  width: 90%;
  border-bottom: 1px solid #fff;
}

.copyright {
  display: flex;
  align-items: center;
  margin: 5px 0px;
  font-family: "Montserrat", sans-serif;
  font-size: 10px;
}

